@media (max-width: 768px) {
	.header {
		&__link {
			font-size: 1.8vh;
		}
	}
	.helloText__text {
		font-size: 2.5vh;
	}
	.about{
		&__wrapper{
			flex-direction: column;
			align-items: center;
			height: 90vh;
		}
		&__photo {
			max-width: 200px;
			max-height: 200px;
			margin-bottom: 10px;
			&-item {
				width: 200px;
				height: 200px;	
			}
		}
		&__me {
			width: 100%;
			p {
				height: 100%;
			}
		}
	}
	.sertify {
		&__item {
			max-width: 50%;
			max-height: 50%;
		}
	}
	.contact {
		&__title {
			font-size: 3vh;
			span {
				font-size: 4vh;
			}
		}
		&__subtitle {
			font-size: 3vh;
		}
	}
}