.works {
	height: 90vh;
	&__wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: rgba(0, 0, 0, .5);
		box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, .3);
	}
	&__item {
		width: 70%;
		height: 100%;
		overflow: auto;
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px;
		}
	}
	&__img {
		position: relative;
		width: 100%;
		max-height: 100%;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
		}
		&_before {
			opacity: 0;
			width: 50%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: url(../images/sprites/svg/zoom.svg) center no-repeat;
			background-color: rgba(0, 0, 0, .5);
			background-size: 90% 100%;
			cursor: pointer;
		}
		&_after {
			opacity: 0;
			width: 50%;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			background: url(../images/sprites/svg/site.svg) center no-repeat;
			background-color: rgba(0, 0, 0, .5);
			background-size: 90% 100%;
			cursor: pointer;
		}
	}
	&__modal{
		display: none;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		bottom: 0;
		left: 50%;
		right: 0;
		transform: translate(-50%, -50%);
		width: 80vw;
		height: 80vh;
		background-color: rgba(0, 0, 0, .9);
		border: 1px solid rgba(0, 0, 0, .4);
		box-shadow: 1px 1px 5px 1px $color-white;
		z-index: 5;
		&-404 {
			&-number {
				margin-bottom: 0;
				font-size: 6vw;
				font-weight: 800;
				text-align: center;
				color: $color-white;
				span {
					color: $color-green;
				}
			}
			&-text {
				text-align: center;
				font-size: 3vw;
				font-weight: 500;
				color: $color-white;
					span {
						color: $color-green;
						font-weight: 800;
						text-transform: uppercase;
					}
			}
		}
		&-buttons {
			width: 30%;
			height: 10%;
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			p {
				margin: 0;
				font-size: 2vw;
				font-weight: 800;
				color: $color-white;
			}
			button {
				border: none;
				background-color: inherit;
				font-size: 1.5vw;
				color: $color-green;
				outline: none;
				transition: all .5s;
				&:hover {
					color: $color-white;
					text-shadow: 2px 2px 5px $color-green;
				}
			}
		}
	}
}