.about {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	align-content: center;
	height: 90vh;
	user-select: none;
	&__wrapper {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
		padding: 5vh 3vw 0 3vw;
		background-color: rgba(0, 0, 0, .5);
		box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, .3);
	}
	&__photo {
		display: flex;
		flex-direction: column;
		align-items: center;
		&-item {
			max-width: 300px;
			max-height: 250px;
			img {
				width: 100%;
				height: 100%;

			}
		}
	}
	&__me {
		overflow: auto;
		width: 80%;
		height: 100%;
		color: $color-white;
		font-size: 1.7em;
		font-weight: 500;
		p {
			margin: 0;
			width: 100%;
			height: 80%;
			span {
				font-weight: 800;
				color: $color-green;
			}
			a {
				font-weight: 800;
				color: $color-green;
				&:hover {
					color: $color-white;
					text-shadow: 3px 3px 10px $color-green;
				}
			}
		}
	}
}