.sertify {
	&__wrapper {
		width: 100%;
		height: 90vh;
		background-color: rgba(0, 0, 0, 0.5);
		box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.3);
	}
	&__items {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
	&__item {
		max-width: 30%;
		max-height: 30%;
		img {
			display: block;
			width: 100%;
			height: 100%;
		}
	}
}