.contact {
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-content: center;
		width: 100%;
		height: 90vh;
		background-color: rgba(255, 255, 255, 0.2);
		box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, .3);
		text-align: center;
	}
	&__title {
		font-size: 1.5vw;
		color: $color-white;
		margin-bottom: 30px;
		border-bottom: 1px solid $color-green;
		padding-bottom: 30px;
		span {
			color: $color-green;
			font-size: 2vw;
			font-weight: 800;
		}
	}
	&__social {
		width: 100%;
		max-height: 10vh;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		border-bottom: 1px solid $color-green;
		padding-bottom: 30px;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			&:hover {
				border-radius: 25%;
				box-shadow: 1px 1px 2px 1px $color-black;
			}
		}
		img {
			max-width: 10vw;
			max-height: 10vh;
		}
	}
	&__subtitle {
		font-size: 1.5vw;
		color: $color-white;
		font-weight: 300;
	}
}